// Initialize Firebase
const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const analytics = getAnalytics(app);
const databaseURL = "https://cs378-p4-d3126-default-rtdb.firebaseio.com/";

function Home() {

    const [gen, setGen] = useState(1)
    const [pokeList, setPokeList] = useState([])
    const [search, setSearch] = useState("")
    const [genList, setGenList] = useState([])
    const [favList, setFavList] = useState([])
    // const [user, loading, error] = useAuthState(auth);
    // console.log(user)

    useEffect(() => {
        loadGen(gen, setGen)
        filterSearch(search)
        console.log("r")
    }, []);
    const logInWithEmailAndPassword = async (email, password) => {
        try {
            await signInWithEmailAndPassword(auth, email, password);
        } catch (err) {
            console.error(err);
            alert(err.message);
        }
    };
    const logout = () => {
        signOut(auth);
    };
    function filterSearch(param) {
        const filtered = genList.filter(pokemon => (pokemon.name).includes(param))
        console.log(filtered)
        setSearch(param)
        setPokeList(filtered)
    }
    function handleFav(pokename) {
        let temp = [...favList]
        const index = temp.indexOf(pokename)
        if (index > -1) {
            temp.splice(index, 1)
        } else {
            temp.push(pokename)
        }
        setFavList(temp)
        // console.log("edited list")
        // console.log(temp)
    }
    async function loadGen(genn, setGen) {
        const temp = await fetchGenData(genn)
        setPokeList(temp)
        setGenList(temp)
        setGen(genn)
    }
    function capitalizeString(string) {
        return string.charAt(0).toUpperCase() + string.slice(1)
    }
    function formatAbilities(abilities) {
        let formatted = capitalizeString(abilities[0])
        for (let i = 1; i < abilities.length; i++) {
            formatted += " / " + capitalizeString(abilities[i])
        }
        return formatted
    }

    function FavStar(pokename) {
        if (favList.indexOf(pokename.pokename) > -1) {
            return (<StarFill className="p0 m0 star" size={30} onClick={() => handleFav(pokename.pokename)}></StarFill>)
        } else {
            return (<Star className="p0 m0 star" size={30} onClick={() => handleFav(pokename.pokename)}></Star>);
        }
    }
    return (
        <>
            <h1>Pokemon Gens I-III</h1>
            <ButtonGroup className="pb-3" aria-label="Basic example">
                <Button variant="outline-primary" active={gen === 1} onClick={() => { loadGen(1, setGen) }}>Gen I</Button>
                <Button variant="outline-primary" active={gen === 2} onClick={() => { loadGen(2, setGen) }}>Gen II</Button>
                <Button variant="outline-primary" active={gen === 3} onClick={() => { loadGen(3, setGen) }}>Gen III</Button>
            </ButtonGroup>
            <ButtonGroup className="pb-3" aria-label="User Accounts">
                <Button variant="outline-primary"  onClick={() => { logInWithEmailAndPassword("usera@gmail.com", "passworda")}}>User A</Button>
                <Button variant="outline-primary"  onClick={() => { logInWithEmailAndPassword("userb@gmail.com", "passwordb")}}>User B</Button>
                <Button variant="outline-primary"  onClick={() => { logInWithEmailAndPassword("userc@gmail.com", "passwordc")}}>User C</Button>
                <Button variant="outline-primary"  onClick={() => { logout() }}>Logout</Button>
            </ButtonGroup>
           
            <Form>
                <Form.Group className="m-4" controlId="exampleForm.ControlInput1">
                    <Form.Label>Search By Name for Gen</Form.Label>
                    <Form.Control placeholder="search pokemon name" onChange={e => filterSearch(e.target.value)} />
                </Form.Group>
            </Form>
            <Row className="g-3 justify-content-center" xs='auto'>
                {
                    pokeList.map(pokemon => {
                        return (
                            <Col key={pokemon.name}>
                                <FavStar pokename={pokemon.name} />
                                <Card style={{ width: '18rem', height: '27rem' }}>
                                    <Card.Img variant="top" src={pokemon.sprite} />
                                    <Card.Body>
                                        <Card.Title>{(pokemon.name).charAt(0).toUpperCase() + (pokemon.name).slice(1)}</Card.Title>
                                        <Row className="justify-content-center">
                                            {
                                                pokemon.types.map(type => {
                                                    return (
                                                        <Col key={pokemon.name + type}>
                                                            <div className={"type " + type}>{type.toUpperCase()}</div>
                                                        </Col>
                                                    )
                                                })
                                            }
                                        </Row>
                                        <Card.Text className="pb-3">
                                            <strong>Abilities:</strong> {formatAbilities(pokemon.abilities)}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>
                            </Col>
                        )
                    })
                }
            </Row>
        </>
    )
}

export default Home;